/**
 * N-adaptive    (PolyPang )
 *
 *  : alpha + beta = 1
 * -      Side   
 * -   (beta/2) +   (alpha/2) = Side (0.5)
 *
 * :
 * - N  Side    
 * - N  Side    
 *
 * @param n -   (2~8)
 * @returns { alpha:   , beta:   , renderN:  N }
 */
export function getPaddleRatios(n: number): {
  alpha: number
  beta: number
  renderN: number
} {
  // N=2: (N=4)  2  (/)
  if (n === 2) {
    const alpha = 0.5 //    (50%)
    return {
      alpha,
      beta: 1 - alpha, // 0.5 (alpha + beta = 1)
      renderN: 4, //  
    }
  }

  // N=3: ,  
  if (n === 3) {
    const alpha = 0.4 // 40% 
    return {
      alpha,
      beta: 1 - alpha, // 0.6 (alpha + beta = 1)
      renderN: 3,
    }
  }

  // N=4: 
  if (n === 4) {
    const alpha = 0.35 // 35% 
    return {
      alpha,
      beta: 1 - alpha, // 0.65 (alpha + beta = 1)
      renderN: 4,
    }
  }

  // N=5:  ( 1.5  -  )
  if (n === 5) {
    const alpha = 0.45 // 45% 
    return {
      alpha,
      beta: 1 - alpha,
      renderN: 5,
    }
  }

  // N=6:  (  -  )
  if (n === 6) {
    const alpha = 0.45 // 45% 
    return {
      alpha,
      beta: 1 - alpha,
      renderN: 6,
    }
  }

  // N=7:  ( )
  if (n === 7) {
    const alpha = 0.5 // 42% 
    return {
      alpha,
      beta: 1 - alpha,
      renderN: 7,
    }
  }

  // N8:   ( )
  const alpha = 0.5 // 40% 
  return {
    alpha,
    beta: 1 - alpha,
    renderN: n,
  }
}
